@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700,900");
@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.icono {
  box-shadow: -1px 2px 40px -20px rgba(0, 0, 0, 0.75);
  -webkit-box-shadow: -1px 2px 40px -20px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: -1px 2px 40px -20px rgba(0, 0, 0, 0.75);
  text-align: center;
  border-radius: 1rem;
  background-color: #FFFFFF;
  padding: 2rem;
}
.icono h3 {
  text-transform: uppercase;
  margin-top: 0;
  font-weight: 700;
  color: #595959;
}
.icono img {
  height: 10rem;
}