@use '../base/mixins' as m;
@use '../base/variable' as v;

.iconos-nosotros{

    @include m.tablet {
        
        @include m.grid(3, 2rem);

            
        
    }
    
}

.icono{ 
    box-shadow: -1px 2px 40px -20px rgba(0,0,0,0.75);
    -webkit-box-shadow: -1px 2px 40px -20px rgba(0,0,0,0.75);
    -moz-box-shadow: -1px 2px 40px -20px rgba(0,0,0,0.75);
    text-align: center;
    border-radius: 1rem;
    background-color: v.$blanco;
    padding: 2rem;
    h3{text-transform: uppercase;
        margin-top: 0;
        font-weight: 700;
        color: lighten(v.$negro, 35%);}
    img{
        height: 10rem;
    }
}